import "./consiglio.js";
import { Paginazione } from "../../commons/js/render_paginazione.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";

$(document).ready(function () {

    var DtoFiltro = function () {
        this.denominazione = { label: 'denominazione', value: null };
        this.title = { label: 'cig', value: null };
        this.procedura_di_scelta = { label: 'procedura', value: null };
    };

    function visualizzaElencoFile(elenco) {
        if (elenco) {
            let html = '<ul class="list-unstyled">';
            elenco.components.forEach(item => {
                let url = apibase.baseurl + item.properties.address;
                html += '<a href="' + url + '" target="_blank">' + item.title + '</a>'
            })
            html += '</ul>';
            return html;
        }
        return '';
    }

    function visualizzaData(val) {
        let date = new Date(val);
        return StringRenderer.renderData(date, "-");
    }

    function renderColonna(datiColonna) {
        let riga = '';
        for (let colonna of colonne) {
            let valore = null;
            if (colonna.tipoCampo === "property") {
                valore = datiColonna.properties[colonna.campo];
            } else if (colonna.tipoCampo === "component") {
                valore = datiColonna.components[colonna.campo];
            } else {
                valore = datiColonna[colonna.campo];
            }
            if (valore) {
                if (colonna.visualizzaCon) {
                    valore = colonna.visualizzaCon(valore);
                }
            }
            riga += "<td>" + (valore || '') + "</td>";
        }
        return riga;
    }

    function render(result) {

        var contenutoTabella = '';
        if (result != null && result.content != undefined) {
            result.content.forEach(function (single) {
                let riga = '<tr>';
                const datiColonna = renderColonna(single);
                riga += datiColonna;
                riga += "</tr>"
                contenutoTabella += riga;
            });
        }
        $("#tbody").html(contenutoTabella);
        if (result.totalPages == 0) {
            $("#tbody").html('<tr><td colspan="' + colonne.length + '">Nessun risultato corrispondente alla ricerca</td></tr>');
        } else {
            $('#paginazione').html(Paginazione.renderizzaComponente(result, {
                paginationWrapper: 'justify-content-center'
            }));
        }
    }

    $('#bottoneReset').click(function () {
        const vistaFiltro = new DtoFiltro();
        let query = window.location.search;
        for (var item of Object.values(vistaFiltro)) {
            query = query.replace(new RegExp('&' + item.label + '=[^&]*', 'g'), '');
        }
        window.location.search = query;
    });

    function submitTastoInvio(e) {
        if (e.which == 13) {
            submit();
        }
    }

    function submit() {
        const vistaFiltro = new DtoFiltro();
        vistaFiltro.title.value = $('#cig').val();
        vistaFiltro.denominazione.value = $('#denominazione').val();
        vistaFiltro.procedura_di_scelta.value = $('#procedura').val();
        let query = window.location.search;
        for (var item of Object.values(vistaFiltro)) {
            query = query.replace(new RegExp('&?' + item.label + '=[^&]*', 'g'), '');
            if (item.value != null && item.value != "") {
                query += '&' + item.label + '=' + item.value;
            }
        }
        window.location.search = query;
    }

    $('#bottoneCerca').click(submit);
    $('#denominazione').keypress(submitTastoInvio);
    $('#cig').keypress(submitTastoInvio);

    function estrapolaProcedura(key) {
        for (const procedura of procedure) {
            if (procedura.id == key) {
                return procedura;
            }
        }
    }

    function estrapolaFiltro() {
        const filtro = {};
        let cig = urlParams.get('cig');
        if (cig != null) {
            filtro['title'] = cig;
            $('#cig').val(cig).change();
        }
        let denominazione = urlParams.get('denominazione');
        if (denominazione != null) {
            filtro['denominazione'] = denominazione;
            $('#denominazione').val(denominazione).change();
        }
        let proceduraDiScelta = urlParams.get('procedura');
        if (proceduraDiScelta != null) {
            const procedura = estrapolaProcedura(proceduraDiScelta);
            if (procedura) {
                filtro['procedura_di_scelta'] = procedura.id;
                $('#procedura').val(procedura.id + '').change();
            }
        }
        return filtro;
    }


    $('.it-date-datepicker').datepicker({
        inputFormat: ["dd/MM/yyyy"],
        outputFormat: 'dd/MM/yyyy',
    });

    function initSelectProcedure() {
        const selectProcedura = $("#procedura");
        procedure.forEach(function (procedura) {
            selectProcedura.append('<option value="' + procedura.id + '">' + procedura.title + '</option>');
        });
        selectProcedura.selectpicker('refresh');
    }

    var urlParams = new URLSearchParams(window.location.search);

    const modello = [
        { label: "CIG", tipoCampo: "property", campo: "title" },
        { label: "Denominazione", tipoCampo: "property", campo: "denominazione" },
        { label: "Avviso di pre-informazione/atti a contrarre", tipoCampo: "component", campo: "avvisi_di_pre_informazione", visualizzaCon: visualizzaElencoFile },
        { label: "Avvisi e bandi", tipoCampo: "component", campo: "avvisi_e_bandi", visualizzaCon: visualizzaElencoFile },
        { label: "Procedura di scelta del contraente", tipoCampo: "property", campo: "procedura_di_scelta" },
        { label: "Avviso sui risultati procedura", tipoCampo: "component", campo: "avvisi_sui_risultati", visualizzaCon: visualizzaElencoFile },
        { label: "Affidamenti", tipoCampo: "component", campo: "affidamenti", visualizzaCon: visualizzaElencoFile },
        { label: "Provvedimenti di ammissione/esclusione", tipoCampo: "component", campo: "provvedimenti", visualizzaCon: visualizzaElencoFile },
        { label: "Composizione commissione/curricula", tipoCampo: "component", campo: "composizione", visualizzaCon: visualizzaElencoFile },
        { label: "Contratti", tipoCampo: "component", campo: "contratti", visualizzaCon: visualizzaElencoFile },
        { label: "Resoconti", tipoCampo: "component", campo: "resoconti", visualizzaCon: visualizzaElencoFile },
        { label: "Ulteriore documentazione", tipoCampo: "component", campo: "ulteriore_documentazione", visualizzaCon: visualizzaElencoFile }
        //{ label: "Data ultimo aggiornamento", tipoCampo: "field", campo: "lastModifiedDate", visualizzaCon: visualizzaData }
    ];

    let intestazioneTabella = '<tr>';
    let colonne = modello.filter(item => !item.optional);
    for (var colonna of colonne) {
        intestazioneTabella += "<th>" + colonna.label + "</th>";
    }
    intestazioneTabella += "</tr>"
    $("#thead").html(intestazioneTabella);

    const procedure$ = new Promise(function (resolve, reject) {
        $.ajax({
            url: apibase.baseurl + '10302?size=50',
            success: function (resultProcedure) {
                if (resultProcedure && resultProcedure.content) {
                    procedure = resultProcedure.content;
                }
                resolve();
            },
            error: reject
        });
    })

    let procedure = [];

    procedure$.then(function () {
        initSelectProcedure();
        var url_to_call = apibase.baseurl + "10306?size=20";
        url_to_call += Paginazione.inizializzaUrl(urlParams);
        const filtro = estrapolaFiltro();
        $.ajax({
            method: 'POST',
            url: url_to_call,
            data: JSON.stringify(filtro || {}),
            success: render,
            contentType: 'application/json',
            dataType: 'json'
        });
    })


    $.ajax({
        method: 'POST',
        url: apibase.baseurl + 'dda/10300',
        success: function (risposta) {
            const data = new Date(risposta);
            const dataFormattata = StringRenderer.renderData(data, '/');
            $('#dataUltimoAggiornamento').html(dataFormattata);
        }
    });

});